.workshops-grid{display:grid;grid-gap:20px;gap:20px;grid-template-columns:1fr;padding:20px}.workshop-card{background-color:#fff;border:1px solid #ccc;border-radius:5px;box-shadow:0 2px 5px rgba(0,0,0,.1);display:flex;flex-direction:column;justify-content:space-between;margin:auto;max-width:300px;overflow:hidden;padding:0;position:relative;width:100%}.workshop-image{height:auto;-o-object-fit:cover;object-fit:cover;width:100%}.workshop-title-container{align-items:center;display:flex;gap:10px;height:4rem;padding:0 1.5rem}.workshop-title{color:#000;flex:1;font-size:1.2rem;line-height:1.5;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.workshop-avatar{border-radius:50%;height:40px;-o-object-fit:cover;object-fit:cover;width:40px}.workshop-info{align-items:center;display:flex;justify-content:space-between}.date,.location,.price{flex:1;font-size:1rem;text-align:center}@media (min-width:480px){.workshops-grid{grid-template-columns:repeat(2,1fr)}}@media (min-width:768px){.workshops-grid{grid-template-columns:repeat(3,1fr)}}@media (min-width:1200px){.workshops-grid{grid-template-columns:repeat(4,1fr)}.workshop-card{flex:1 1 calc(25% - 20px);max-width:none}}@media (max-width:480px){.workshop-card{max-width:100%}.workshop-image{height:200px}}